<template>
    <div class="barCharts">
        <div id="barCharts" ref="barCharts"></div>
    </div>
</template>

<script>
  import * as echarts from 'echarts';
  //这是绘制图表的参数定义
  let optionTemp =
    {
      tooltip: {
        trigger: 'axis',
        axisPointer: {
          type: 'cross',
          crossStyle: {
            color: '#999'
          }
        }
      },
      grid: {
        left: '2%',
        top: '42',
        right: '4%',
        bottom: '6',
        containLabel: true
      },
      legend: {
        // show:false,
        data: [
          // 'legend1', 'legend2'
          {icon:'circle',name:'legend1'},
          {icon:'rect',name:'legend2'},
        ],
        x:'right',      //可设定图例在左、右、居中
        y:'top',     //可设定图例在上、下、居中
        padding:[0,50,0,0],   //可设定图例[距上方距离，距右方距离，距下方距离，距左方距离
        textStyle: {color: '#fff'},
      },
      xAxis: [
        {
          type: 'category',
          data: [0,0],
          axisPointer: {
            type: 'shadow'
          },
          axisTick: { //x轴刻度线
            show: false,
          },
          axisLine: { //x轴
            show: false,
            // lineStyle:{
            //   color:'#00FF00',
            // }
          },
          axisLabel: { //设置x轴的字
            show: true,
            interval:0,//使x轴横坐标全部显示
            // rotate:50,//旋转
            textStyle: {//x轴字体样式
              color: "#fff",
              margin: 15,
              fontSize:12
              // fontWeight: 'bold',
            }
          },
        }
      ],
      yAxis: [
        {
          type: 'value',
          name: '单位：个',
          interval: 20,
          max: null,
          nameTextStyle: {
            color: "#66B9FF",
          },
          axisLabel: { //设置y轴的字
            // show: true,
            // formatter: '{value} ml',
            color: '#fff',
            interval: 0,//使y轴横坐标全部显示
            textStyle: {//y轴字体样式
              color: "#66B9FF",
              fontSize: 12,
              margin: 20,
            }
          },
          splitLine: {//网格线样式
            show: true,
            lineStyle: {
              color: '#0065B3',
              width: 1,
              type: 'solid'
            }
          },
          axisTick: { //y轴刻度线
            show: false,
          },
          axisLine: { //y轴
            show: false,
          },
        },
      ],
      series: [
        {
          name: 'legend1',
          type: 'bar',
          data: [20,10,30,60,70],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#1268f3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#08a4fa" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#01ccfe" // 100% 处的颜色
              }], false)
            }
          },
        },
        {
          name: 'legend2',
          type: 'bar',
          data: [20,10,30,60,70]
        },
      ]
    }


  export default {
    name: "barCharts",
    props: {
      yStyle:{
        type:Number,
        default:()=>{
          return null
        }
      },
      legend:{
         type:Object,
        default:()=>{
          return {}
        }
      },
      chartMax:{
        type:Number,
        default:()=>{
          return null
        }
      },
      dataZoom:{
        type:Array,
        default:()=>{
          return []
        }
      },
      index:{
        type:Number,
        default:()=>{
          return null
        }
      },
      xData:{
        /*** * xData:['一月',二月]*/
        type:Array,
        default:()=>{
          return []
        }
      },
      seriesData: {
        /*** * seriesData:[{
          name: 'legend1',
          type: 'bar',
          data: [20,10,30,60,70],
          itemStyle: {
            normal: {
              color: new echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#1268f3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#08a4fa" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#01ccfe" // 100% 处的颜色
              }], false)
            }
          },
        },]*/
        type: Array,
        default: () => {
          return []
        }
      },
      //  决定x轴和y轴显示(单位)
      _zb: {
        /** :['单位','']*/
        type: Array,
        default: () => {
          return ['单位', '']
        },

        //echarts 样式
        legendStyle:{
          type:Object,
          default:()=>{
            return {color: '#333333'}
          }
        },
        xStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
              fontWeight: '',
            }
          }
        },
        yStyle:{
          type:Object,
          default:()=>{
            return {
              color: "#333333",
              margin: 15,
            }
          }
        },
      },

    },
    watch: {
      seriesData: {//深度监听，可监听到对象、数组的变化
        handler() {
          window.console.log(12355)
          this.init()
        },
        deep: true, //true 深度监听
        //immediate: true //开启首次监听赋值（不开启首次监听不到）
      }
    },
    data() {
      return {
        option: optionTemp,
        myChart:null,
      }


    },
    mounted() {
      this.init();
    },
    methods: {
      dataSet(){
        this.option.yAxis[0].name = this._zb[0]
        this.option.xAxis[0].name = this._zb[1]
        this.option.xAxis[0].data = this.xData
        this.option.yAxis[0].interval = this.yStyle
        this.option.legend.show = this.legend.legend

        if (this.legend.padding2){
          this.option.legend.padding = this.legend.padding2
        }
        this.option.yAxis[0].max = this.chartMax
        this.option.dataZoom = this.dataZoom
        if (this.seriesData.length>0){
          this.option.legend.data = []
          this.seriesData.forEach(item=>{
            this.option.legend.data.push(item.name)
          })
          this.option.series = this.seriesData
        }
        if(this.seriesData[0].data.length > 7&&this.seriesData[0].data){
          this.option.xAxis[0].axisLabel.formatter = function(params){
            var val = ''
            if(params.length > 4){
              val = params.substr(0,4) + '...';
              return val;
            }else{
              return params;
            }
          }
        }

      },
      init() {
        this.dataSet()
        let mychart = echarts.init(this.$refs.barCharts, );
        //将会绘制图表的参数加到图表中

        mychart.setOption(this.option,true);
        let $this = this;
        mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
          // window.console.log(params);
        });
        window.addEventListener("resize", ()=>{
          mychart.resize()
        });
      },
    },
    beforeDestroy() {
      /* 页面组件销毁的时候，别忘了移除绑定的监听resize事件，否则的话，多渲染几次
      容易导致内存泄漏和额外CPU或GPU占用哦*/
      window.removeEventListener("resize", () => {
        this.myChart.resize();
      });
    },

  }
</script>

<style scoped lang="scss">
    .barCharts {
        height: 100%;
        #barCharts {
            height: 100%;
            /*width: 97%;*/
            /*height: 99%;*/
        }
    }
</style>